@import url(https://fonts.googleapis.com/css2?family=Stint+Ultra+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200&display=swap);
.infoBar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #2979ff;
  border-radius: 4px 4px 0 0;
  height: 60px;
  width: 100%;
}

.leftInnerContainer {
  flex: 0.5 1;
  display: flex;
  align-items: center;
  margin-left: 5%;
  color: white;
}

.rightInnerContainer {
  display: flex;
  flex: 0.5 1;
  justify-content: flex-end;
  margin-right: 5%;
}

.onlineIcon {
  margin-right: 5%;
}

.form {
  display: flex;
  flex-direction: row;
  border-top: 2px solid #d3d3d3;
}

.input {
  border: none;
  border-radius: 0;
  padding: 5%;
  flex: 0.8 1;
  font-size: 1.2em;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
}

.sendButton {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  background: #2979ff;
  border-radius: 5px;
  font-weight: bold;
  padding: 10px;
  margin: 5px;
  display: inline-block;
  border: none;
  flex: 0.2 1;
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  .form {
    flex-direction: column;
  }
}

.messageBox {
    background: #F3F3F3;
    border-radius: 20px;
    padding: 5px 20px;
    color: white;
    display: inline-block;
    max-width: 80%;
  }
  
  .messageText {
    width: 100%;
    letter-spacing: 0;
    float: left;
    font-size: 1.1em;
    word-wrap: break-word;
  }
  
  .messageText img {
    vertical-align: middle;
  }
  
  .messageContainer {
    display: flex;
    justify-content: flex-end;
    padding: 0 5%;
    margin-top: 3px;
  }
  
  .sentText {
    display: flex;
    align-items: center;
    font-family: Helvetica;
    color: #828282;
    letter-spacing: 0.3px;
  }
  
  .pl-10 {
    padding-left: 10px;
  }
  
  .pr-10 {
    padding-right: 10px;
  }
  
  .justifyStart {
    justify-content: flex-start;
  }
  
  .justifyEnd {
    justify-content: flex-end;
  }
  
  .colorWhite {
    color: white;
  }
  
  .colorDark {
    color: #353535;
  }
  
  .backgroundBlue {
    background: #2979FF;
  }
  
  .backgroundLight {
    background: #F3F3F3;
  }
.messages {
    height: 60px;
    padding: 5% 0;
    overflow: auto;
    flex: auto;
  }
.companyName {
    font-family: 'Stint Ultra Condensed', cursive;
    font-size: 30px;
    font-weight: lighter !important;
    letter-spacing: 3px;
    color: #3ad435;
}
.textContainer {
  display: flex;
  flex-direction: column;
  margin-left: 100px;
  color: white;
  height: 60%;
  justify-content: space-between;
}

.activeContainer {
  display: flex;
  align-items: center;
  margin-bottom: 50%;
}

#para {
  font-size: 25px;
  color: #ff5b4f;
  font-weight: 900;
  padding-bottom: 5px;
}

.activeItem {
  display: flex;
  align-items: center;
}

.activeContainer img {
  padding-left: 10px;
}

.textContainer h1 {
  margin-bottom: 0px;
}

@media (min-width: 320px) and (max-width: 1200px) {
  .textContainer {
    display: none;
  }
}

.outerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #ffffff;
  border-radius: 8px;
  height: 80%;
  width: 50%;
}

.activeItem {
  display: flex;
  flex-direction: row;
}

@media only screen and (max-width: 480px) {
  .container {
    width: 100vw;
    height: 80vh;
  }
}

@media only screen and (max-width: 1200px) {
  .container {
    width: 60%;
  }
}

* {
  font-family: "Nunito", sans-serif;
}

.joinOuterContainer {
  height: 100vh;
  display: grid;
  place-items: center;
}

.mainHeader {
  max-width: 80vw;
  text-align: center;
}

.mainHeader__para {
  font-size: 25px;
  color: #ff5b4f;
  font-weight: 900;
  padding-bottom: 5px;
  border-bottom: 1px solid white;
}

.joinInnerContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.heading {
  color: white;
  font-size: 2.5em;
  padding-bottom: 5px;
  border-bottom: 2px solid white;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

input {
  width: 30vw;
  height: 40px;
  margin: 5px;
  outline: 0;
  padding: 5px;
  border: 2px groove #2c36bf;
  border-radius: 5px;
  font-weight: 900;
  font-size: 18px;
}

.joinBtn {
  width: 20vw;
  margin: 5px;
  height: 50px;
  background-color: #2c76bf;
  color: white;
  font-size: 25px;
  cursor: pointer;
  border: none;
  font-weight: 900;
  border-radius: 5px;
  transition: all 0.3s ease-in-out;
}

.joinBtn:hover {
  background-color: #288bed;
}

@media only screen and (max-width: 650px) {
  input {
    width: 50vw;
  }
  .joinBtn {
    width: 45vw;
  }
}

html,
body {
  padding: 0;
  margin: 0;
  background-color: #11204d;
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 4px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

